var mavonEditor = require('mavon-editor')
import "mavon-editor/dist/css/index.css";
import marked from "marked";
marked.setOptions({
  renderer: new marked.Renderer(),
  gfm: true,
  tables: true,
  breaks: false,
  pedantic: false,
  sanitize: false,
  smartLists: true,
  smartypants: false
});
interface RuleFormValue{
  title: string,
  cate: string,
  summary: string,
  content: string,
  fileList: string,
}

import {Component,Vue} from "vue-property-decorator"
@Component({
components:{
  'mavon-editor': mavonEditor.mavonEditor
}
})
export default class Add extends Vue{
   fileList:any=[]
   configs:object={}
   ruleForm:RuleFormValue= {
   title: "",
   cate: "",
   summary: "",
   content: "",
   fileList: "",
   }
   rules:object={
        title: [
          { required: true, message: "请输入文章标题", trigger: "blur" },
          { min: 3, max: 20, message: "长度在 3 到 20 个字符", trigger: "blur" }
        ],
        cate: [{ required: true, message: "请选择分类", trigger: "change" }],
        summary: [
          { required: true, message: "请填写文章简介", trigger: "blur" }
        ]
      }
    changeMavon() {}
    $imgAdd() {}
    handleRemove(file, fileList) {
      console.log(file, fileList);
    }
    handlePreview(file) {
      console.log(file);
    }
    //提交文章
    submitForm(formName) {
      (this as any).$refs[formName].validate(valid => {
        if (valid) {
          let formData = new FormData();
          let file = (this as any).$refs.upload.uploadFiles[0];
          let config = {       
            headers: { "Content-Type": "multipart/form-data" }
          };

          if (!file) {
            // 若未选择文件
            (this as any).message({
              message: "请添加文件",
              type: "warning"
            });
            return;
          }
          let { title, cate, summary, content } = this.ruleForm;

          formData.append("file", file.raw);
          formData.append("title", title);
          formData.append("cate", cate);
          formData.append("summary", summary);
          formData.append("content", marked(content));
          console.log(formData.get("file"));
          (this as any).$API.articleModel.AddArticle(formData,config)
            .then(() => {
              (this as any).$message({
                message: "添加成功",
                type: "success"
              });
            })
            .catch(err => {
              if (err) {
                (this as any).$message({
                  message: "添加失败",
                  type: "error"
                });
              }
            });
        } else {
          (this as any).$message({
            message: "请填写正确信息",
            type: "warning"
          });
          return false;
        }
      });
    }
    resetForm(formName) {
      (this as any).$refs[formName].resetFields();
    }
  }
